export default {
	data() {
		return {
			"instruments": [{
					"ID": 1,
					"InstrumentCode": "22222222",
					"FacilitiesName": "dfdfsafdsa"
				}

			],

			"instrumentCode": "111",
			"userName": "",
			"userPhone": ""
		}
	},
	methods: {
		submit: function() {

			if (this.userName === "" || this.userPhone == "") {
				uni.showModal({
					title: "提示",
					content: "请输入您的姓名和联系电话",
					showCancel: false

				})
			}


		},
		add: function() {
			uni.showToast({
				title: "添加"
			})
		},
		requestInstrumentInfo: function() {
			uni.showToast({
				title: this.instrumentCode
			})
		}
	}
}
